import React, { useMemo } from "react";
import { FuseResultMatch } from "react-use-fusejs";
import styled from "styled-components";

import { styles as typographyStyles } from "../../../theme/Typography";

const Wrapper = styled.div`
  color: var(--color-grey-600);

  span {
    ${typographyStyles.Body}
  }

  strong {
    color: #ff0000;
    margin: 0 4px;
  }
`;

export type SearchContentProps = {
  keyword: string;
  matches: readonly FuseResultMatch[];
  className?: string;
};

const SearchItemContent: React.FC<SearchContentProps> = ({
  keyword,
  matches,
  className,
}) => {
  const paragrahs = useMemo(() => {
    // find body match rather than title, tag, category match
    const bodyBestObj = (matches.find((v) => v.key === "body") || matches[0])!;

    // find full match rather than partial match
    const [st, ed] = (bodyBestObj.indices.find(
      ([si, ei]) => ei + 1 - si === keyword.length
    ) || bodyBestObj.indices[0])!;

    const N = 50;

    // return content with previous and next N words
    return [
      bodyBestObj.value!.substring(st - N, st),
      bodyBestObj.value!.substring(st, ed + 1),
      bodyBestObj.value!.substring(ed + 1, ed + N),
    ];
  }, [keyword, matches]);

  return (
    <Wrapper className={className}>
      <span>...{paragrahs[0]}</span>
      <strong>{paragrahs[1]}</strong>
      <span>{paragrahs[2]}...</span>
    </Wrapper>
  );
};

export default SearchItemContent;
