import React, { useEffect, useMemo, useState } from "react";
import { graphql, useStaticQuery } from "gatsby";
import { FuseJsIndexData, useGatsbyPluginFusejs } from "react-use-fusejs";
import { FuseMdx } from "gatsby-theme-advanced/src/types";
import { getImage } from "gatsby-plugin-image";

import { Helmet } from "react-helmet";
import { useConfig } from "gatsby-theme-advanced";

import Layout from "../../../layouts";
import Breadcrumb from "../../../components/Breadcrumb";
import * as S from "./styles";
import SearchItemContent from "./SearchContent";
import AuthorSegment from "../../../components/AuthorSegment";

import SearchIcon from "../../../../images/eyeglass.png";

const SearchResultPage: React.FC = () => {
  const config = useConfig();

  const data = useStaticQuery<{ fusejs: FuseJsIndexData<FuseMdx> }>(graphql`
    query SearchAmaranthQuery {
      fusejs {
        index
        data
      }
    }
  `);

  const [query, setQuery] = useState<string>("");

  useEffect(() => {
    // get keyword from url search
    const search = new URLSearchParams(window.location.search);
    const keyword = search.get("k");
    setQuery(keyword || "");
  }, []);

  // get search result (default postive order by score)
  const results = useGatsbyPluginFusejs<FuseMdx>(query, data.fusejs, {
    includeScore: true,
    includeMatches: true,
    threshold: 0,
    ignoreLocation: true,
  });

  // reverse the result
  const reveseResults = useMemo(() => [...results].reverse(), [results]);

  // console.log("result is", reveseResults);
  return (
    <Layout showSidebar>
      <S.Wrapper>
        <Helmet title={`搜索结果：${query} | ${config.website.title}`} />

        <Breadcrumb breadcrumbs={[{ label: `「${query}」的搜索结果` }]} />

        <S.ListHead>
          <S.IndIcon src={SearchIcon} alt="search" />
          {`搜索「${query}」`}
        </S.ListHead>

        <S.ResultBlock>
          {reveseResults.map(({ item, score, matches }) => (
            <S.ResultBox key={item.id} to={item.slug}>
              <S.ResultCover
                image={getImage(item.coverImg)!}
                objectFit="contain"
                alt={item.title}
              />

              <S.ResultContentWrap>
                <S.ResultContentTop>
                  <h4>{item.title}</h4>
                  <S.ResultScore>匹配度：{score?.toFixed(2)}</S.ResultScore>
                </S.ResultContentTop>

                <SearchItemContent keyword={query} matches={matches!} />
              </S.ResultContentWrap>
            </S.ResultBox>
          ))}
          <S.ResultTotal>
            共有<strong>{reveseResults.length}</strong>个搜索结果
          </S.ResultTotal>
        </S.ResultBlock>

        <S.AuthorDiv>
          <AuthorSegment />
        </S.AuthorDiv>
      </S.Wrapper>
    </Layout>
  );
};

export default SearchResultPage;
