import styled from "styled-components";
import { GatsbyImage } from "gatsby-plugin-image";

import { breakpoints } from "../../../theme/Constants";
import { TransparentLink } from "../../../components";
import { H2 } from "../../../theme";

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 60px;
  padding: 0;

  @media (max-width: ${breakpoints.sm}) {
    gap: 32px;
    padding: 0 16px;
  }
`;

export const ListHead = styled(H2)`
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: bolder;
`;

export const IndIcon = styled.img`
  width: 60px;
  height: auto;
  margin-right: 24px;

  @media (max-width: ${breakpoints.sm}) {
    width: 40px;
    margin-right: 8px;
  }
`;

export const ResultBlock = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;

  @media (max-width: ${breakpoints.sm}) {
    gap: 16px;
  }
`;

export const ResultBox = styled(TransparentLink)`
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  background-color: white;
  box-shadow: 4px 4px #ddd;
  padding: 24px;
  gap: 24px;
  transition: all 0.4s ease;

  @media (max-width: ${breakpoints.sm}) {
    padding: 16px;
    gap: 16px;
  }

  :hover {
    transform: translate(-4px, -4px);
    box-shadow: 8px 8px #ddd;
  }
`;

export const ResultCover = styled(GatsbyImage)`
  flex: 0 0 30%;
  max-height: 200px;
  align-self: center;
  background-color: #f7f7f7;

  @media (max-width: ${breakpoints.lg}) {
    display: none;
  }
`;

export const ResultContentWrap = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 12px;
`;

export const ResultContentTop = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
`;

export const ResultScore = styled.span`
  color: var(--color-grey-200);
  font-size: 14px;
`;

export const ResultTotal = styled.div`
  color: var(--color-grey-600);
  font-size: 14px;
  align-self: flex-end;
`;

export const AuthorDiv = styled.div`
  align-self: center;
`;
